.pagination {
    @include ulClean;
    display: block;
    text-align: center;
    min-height: 7.6rem;

    li {
        @include fontSize(32,19,20);
        display: inline-block;
        text-align: center;
        padding: 5rem 5.46vw;
        @include breakpoint(tabletAndDesktop) {
            padding: 3rem calcVw(15, 768);
        }
        @include breakpoint(desktopOnly) {
            padding: 1.5rem calcVw(15, 1700);
        }

        &.hidden--until-td {
            display: none;
            @include breakpoint(tabletAndDesktop) {
                display: inline-block;
            }

        }

        &.active a {
            @include circled-icon(11.875vw);
            background: $lightGray;
            display: inline-block;
            @include breakpoint(tabletAndDesktop) {
                @include circled-icon(6.25vw);
            }
            @include breakpoint(desktopOnly) {
                @include circled-icon(3vw);
            }
        }

    }
    
    li:first-child {
        padding-left: 0;
        }
    li:last-child {
        padding-right: 0;
    }

}
.no-pad--right {
    padding-right: 0;
}