.button {
    background-color: $yellow;
    border: none;
    color: $black;
    text-align: center;
    padding: 0.3rem 1.4rem;
    display: inline-block;
    font-weight: bold;
    text-transform: uppercase;
    @include fontSize(12,8,12);
    @include lineHeight(34,18.39,36);
    @include letterSpacing(50,50,50);

    &--white-bg {
        background: white;
        padding-left: 2.1rem;
        padding-right: 2.1rem;
        @include letterSpacing(100,100,100);

        &:active {
            background-color: $black;
            color: $white;
        }
    }

    &--caption {
        position: absolute;
        bottom: 0;
        cursor: default;
    }
}


.button-alt {
    text-align: center;
    background-color: $white;
    padding: 1rem 2rem;
    @include fontSize(15,10.8,12);
    @include lineHeight(36,32.4,36);
    @include letterSpacing(100,100,100);
    border: 0.1rem solid rgba(0,0,0,0.1);
    -webkit-box-shadow: 0px 2px 24px -2px rgba(0,0,0,0.76); 
    box-shadow: 0px 2px 24px -2px rgba(0,0,0,0.76);
    cursor: pointer;

    &:active {
        background-color: $black;
        color: $white;
    }
}

.button--shp {
    padding: 0.4rem 3rem;
    line-height: 2.6rem;
    @include breakpoint(tabletAndDesktop) {
        padding: 0.4rem 1.5rem;
        line-height: 1.6rem;
        @include breakpoint(desktopOnly) {
            padding: 0.4rem 3rem;
            line-height: 2.5rem;            
        }
    }
}